*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #504945;
  background-color: #0fa;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue;
  line-height: 1.5;
}

img {
  width: 352px;
  height: 240px;
  object-fit: cover;
  border-radius: 2px;
  display: block;
  box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
}

.gallery {
  max-width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin: 0 auto;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  text-align: center;
  background-color: #000;
  margin-bottom: 30px;
  padding: 30px;
}

.search-form input {
  max-width: 100%;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  padding: 8px 15px;
}

.search-form input:hover, .search-form input:focus {
  border: 2px solid #458588;
  outline: none;
}

.search-form button {
  cursor: pointer;
  max-width: 100%;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  padding: 8px 15px;
  font-family: inherit;
}

.search-form button:hover, .search-form button:focus {
  color: #fff;
  background-color: #000;
  border: 2px solid #458588;
  outline: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.load-more {
  cursor: pointer;
  max-width: 100%;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin: 30px auto;
  padding: 8px 15px;
  display: block;
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #000;
  border: 2px solid #458588;
  outline: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.info {
  text-align: center;
  justify-content: space-between;
  display: flex;
}

.info-item {
  margin-bottom: 0;
}

.on-top-button {
  z-index: 1000;
  width: 50px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  background: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: fixed;
  bottom: 3vh;
  right: 1vw;
}

.on-top-button--visible {
  opacity: 1;
  visibility: visible;
}

@font-face {
  font-family: Material Icons;
  font-style: normal;
  font-weight: 400;
  src: url("https://fonts.gstatic.com/s/materialicons/v140/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2") format("woff2");
}

.material-icons {
  letter-spacing: normal;
  text-transform: none;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  --webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
  font-family: Material Icons;
  font-size: 24px;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  display: inline-block;
}

